import {
  pluginName
} from './constants';

const CKEDITOR = window.CKEDITOR;

function getFirstTextNode(element) {
  const children = element.getChildren();
  for (let i = 0; i < children.count(); i++) {
    const child = children.getItem(i);
    if (child.type === CKEDITOR.NODE_TEXT) {
      return child;
    }
  }
  return null;
}

CKEDITOR.plugins.add(pluginName, {
  requires: 'richcombo',
  init: function (editor) {
    const pluginConfig = CKEDITOR.tools.get_plugin_config(pluginName, editor);
    const colors = pluginConfig;

    editor.ui.addRichCombo('ApplyColor', {
      label: 'Apply Color',
      toolbar: 'styles',
      panel: {
        css: [CKEDITOR.skin.getPath('editor')].concat(editor.config.contentsCss),
        multiSelect: false
      },
      init: function () {
        this.add('default', 'Remove Color', 'Remove Color'); // Remove color default option
        console.log('colors', colors);
        for (let label in colors) {
          const colorValue = colors[label];
          this.add(colorValue, label, label);
        }
      },
      onRender: function () {
        this.setValue(''); // Prevents any item from being auto-selected
      },
      onClick: function (value) {
        editor.focus();
/*         if (value === 'default') {
          const selection = editor.getSelection();
          const ranges = selection.getRanges();

          editor.fire('lockSnapshot');

          console.log('🟡 Starting color removal...');

          // Force span splitting by applying and removing a dummy style
          const dummyStyle = new CKEDITOR.style({ element: 'span', styles: { 'background-color': '#ffffff' } });
          editor.applyStyle(dummyStyle);
          editor.removeStyle(dummyStyle);

          const bookmarks = selection.createBookmarks();
          ranges.forEach((range, i) => {
            console.log(`🟢 Range ${i + 1}:`, range);

            let found = 0;
            let unwrapped = 0;

            const tryProcessSpan = (span) => {
              if (
                span &&
                span.type === CKEDITOR.NODE_ELEMENT &&
                span.getName() === 'span' &&
                span.getStyle('color')
              ) {
                found++;
                console.log('🎯 Found span with color:', span.getOuterHtml());
                span.removeStyle('color');

                if (!span.hasAttributes()) {
                  const children = span.getChildren().toArray();
                  for (let child of children) {
                    span.insertBeforeMe(child.remove());
                  }
                  span.remove();
                  unwrapped++;
                  console.log('🧹 Unwrapped span.');
                }
              }
            };

            // 1. Try enclosed node
            const enclosed = range.getEnclosedNode();
            tryProcessSpan(enclosed);

            // 2. Fallback: walker inside the range
            const walker = new CKEDITOR.dom.walker(range);
            walker.evaluator = function (node) {
              return node.type === CKEDITOR.NODE_ELEMENT &&
                    node.getName() === 'span' &&
                    node.getStyle('color');
            };

            let node;
            while ((node = walker.next())) {
              tryProcessSpan(node);
            }

            // 3. NEW: Also check if startContainer or endContainer is a colored span
            tryProcessSpan(range.startContainer);
            tryProcessSpan(range.endContainer);

            console.log(`✅ Range ${i + 1} done: found ${found}, unwrapped ${unwrapped}`);
          });

          editor.fire('unlockSnapshot');

          console.log('🎉 Finished removing color from selection.');
        } */
        if (value === 'default') {
          const selection = editor.getSelection();
          if (!selection) {
            console.warn('⚠️ No selection found.');
            return;
          }

          const bookmarks = selection.createBookmarks();
          const ranges = selection.getRanges();

          if (!ranges.length || ranges.every(r => r.collapsed)) {
            console.warn('⚠️ No text selected.');
            return;
          }

          editor.fire('lockSnapshot');
          console.log('🟡 Starting color removal…');

          ranges.forEach((range, index) => {
            console.log(`📌 Processing range ${index + 1}...`);
            if (range.collapsed) return;

            range.enlarge(CKEDITOR.ENLARGE_INLINE);

            const walker = new CKEDITOR.dom.walker(range);
            walker.evaluator = function (node) {
              return (
                node.type === CKEDITOR.NODE_ELEMENT &&
                node.getName() === 'span' &&
                node.getStyle('color')
              );
            };

            let node;
            let found = false;
            let unwrapped = 0;

            while ((node = walker.next())) {
              found = true;
              console.log('🎯 Found colored span (fully selected):', node.getOuterHtml());

              node.removeStyle('color');

              if (!node.hasAttributes()) {
                const children = node.getChildren().toArray();
                for (const child of children) {
                  node.insertBeforeMe(child.remove());
                }
                node.remove();
                unwrapped++;
                console.log('🧹 Unwrapped empty span.');
              }
            }

            // Fallback: Partially selected span
            if (!found) {
              const colorSpan = range.startContainer.getAscendant('span', true);

              if (
                colorSpan &&
                colorSpan.type === CKEDITOR.NODE_ELEMENT &&
                colorSpan.getStyle('color')
              ) {
                console.log('⚠️ Partial span selected. Attempting to split and remove color.');
                const bookmark = range.createBookmark();

                const fallbackWalker = new CKEDITOR.dom.walker(range);
                fallbackWalker.evaluator = node =>
                  node.type === CKEDITOR.NODE_TEXT &&
                  node.getParent().equals(colorSpan);

                let textNode;
                let splitCount = 0;

                while ((textNode = fallbackWalker.next())) {
                  const text = textNode.getText();
                  const startOffset = textNode.equals(range.startContainer)
                    ? range.startOffset
                    : 0;
                  const endOffset = textNode.equals(range.endContainer)
                    ? range.endOffset
                    : text.length;

                  const before = text.substring(0, startOffset);
                  const middle = text.substring(startOffset, endOffset);
                  const after = text.substring(endOffset);

                  const fragments = [];

                  if (before) {
                    fragments.push(new CKEDITOR.dom.text(before));
                  }

                  if (middle) {
                    const middleText = new CKEDITOR.dom.text(middle);

                    const parentStyles = colorSpan.getAttribute('style') || '';
                    const keptStyle = parentStyles
                      .split(';')
                      .map(s => s.trim())
                      .filter(s => s && !s.startsWith('color'))
                      .join('; ');

                    if (keptStyle) {
                      const span = new CKEDITOR.dom.element('span');
                      span.setAttribute('style', keptStyle);
                      span.append(middleText);
                      fragments.push(span);
                      console.log(`✨ Rebuilt span with kept styles: "${keptStyle}"`);
                    } else {
                      fragments.push(middleText);
                      console.log('🆕 Inserted plain text node (no remaining styles).');
                    }
                  }

                  if (after) {
                    fragments.push(new CKEDITOR.dom.text(after));
                  }

                  for (const frag of fragments) {
                    textNode.insertBeforeMe(frag);
                  }

                  textNode.remove();
                  splitCount++;
                }

                if (splitCount > 0) {
                  console.log(`🪓 Split and rebuilt ${splitCount} node(s) in fallback mode.`);
                }

                if (colorSpan.getChildCount() === 0) {
                  colorSpan.remove();
                  console.log('🧹 Removed empty color span.');
                }

                range.moveToBookmark(bookmark);
                selection.selectRanges([range]);
              } else {
                console.log('ℹ️ No colored span found in fallback case.');
              }
            }

            console.log(`✅ Finished processing range ${index + 1}. Found: ${found}, Unwrapped: ${unwrapped}`);
          });

          selection.selectBookmarks(bookmarks);
          editor.fire('unlockSnapshot');
          console.log('🎉 Finished removing color from selection.');
        }
        else {
          const style = new CKEDITOR.style({
            element: 'span',
            styles: { color: value }
          });
          editor.focus();
          editor.applyStyle(style);
        }
      }
    });
  }
});
